<script>
	import { navigate } from "svelte-routing";
	import { onMount } from 'svelte';

	let nombre = '';

	onMount(() => {
		const iden = JSON.parse(localStorage.getItem('abc_diario_iden'));

		if (iden) {
			navigate("/main", { replace: true });
		}
	});

	function handleContinuar(tipo) {
		console.log(tipo);

		if (nombre.length === 0) {
			alert('Debes proporcionar el nombre de quien juega.');
		} else {
			const iden = {
				nombre,
				avatar: tipo === 1 ? '/abc_diario/assets/images/donna.png' : '/abc_diario/assets/images/uomo.png',
			};

			localStorage.setItem('abc_diario_iden', JSON.stringify(iden));

			navigate("/main", { replace: true });
		}
	}
</script>

<div class="container">
	<div class="row">
		<div class="col center title">
			¿Quién juega?
		</div>
	</div>
	<div class="row">
		<div class="col center">
			<input type="text" class="form-control" bind:value={nombre}>
		</div>
	</div>
	<div class="row">
		<div class="col center title">
			Selecciona un avatar para continuar
		</div>
	</div>
	<div class="row">
		<div class="col center">
			<img src="/abc_diario/assets/images/donna.png" width="135" height="135" alt="Donna" on:click={e => handleContinuar(1)}>
			&nbsp;&nbsp;&nbsp;
			<img src="/abc_diario/assets/images/uomo.png" width="135" height="135" alt="Uomo" on:click={e => handleContinuar(2)}>
		</div>
	</div>
</div>

<style>
	.title {
		margin-top: 2rem;
		margin-bottom: 0.5rem;
		font-size: 24pt;
		font-weight: 400;
	}

	.center {
		text-align: center;
	}
</style>