<script>
	import { navigate } from "svelte-routing";
	import { onMount } from 'svelte';

	let iden = {}; 
	const abecedario = ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'J', 'K', 'L', 'M', 'N', 'Ñ', 'O', 'P', 'Q', 'R', 'S', 'T', 'U', 'V', 'W', 'X', 'Y', 'Z'];

	onMount(() => {
		iden = JSON.parse(localStorage.getItem('abc_diario_iden'));
	});

	function handleSalir() {
		localStorage.removeItem('abc_diario_iden');
		navigate("/", { replace: true });
	}

	async function handleLetraClick(letra) {
		const element = document.getElementById(`sound${letra}`);

		await element.play();
	}
</script>

<div class="container">
	<div class="row">
		<div class="col">
			<header>
				<img src="{iden.avatar}" width="48" height="48" alt="Avatar">
				<div class="titulo">
					<h5 class="text-primary">Hola {iden.nombre}</h5>
					<div class="text-muted">Juega conmigo al abc diario.</div>
				</div>
				<button type="button" class="btn btn-info" on:click={handleSalir}>Salir</button>
			</header>
		</div>
	</div>
	<div class="row separa">
		<div class="col">
			<div class="abecedario">
				{#each abecedario as letra}
					<div class="display-3 letra" on:click={e => handleLetraClick(letra)}>
						<audio id="sound{letra}">
							<source src="/abc_diario/assets/sounds/mp3/{letra.toLowerCase()}.mp3" type="audio/mpeg">
						</audio>
						{letra}
					</div>
				{/each}
			</div>
		</div>
	</div>
</div>

<style>
	header {
		display: flex;
		flex-direction: row;
		justify-content: center;
		border-bottom: 1px solid #b4b4b4;
		padding: 1rem;
	}

	header .titulo {
		flex: 1;
		margin-left: 1rem;
	}

	header .titulo h5 {
		margin-bottom: 0;
	}

	header button {
		margin: 0;
	}

	.separa {
		margin-top: 2rem;
	}

	.abecedario {
		flex: 1;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
	}

	.letra {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 5.5rem;
		height: 5.5rem;
		cursor: pointer;
		margin-right: 0.5rem;
		margin-bottom: 0.5rem;
		border-radius: 0.5rem;
	}

	.letra:hover {
		background-color: rgba(0, 0, 0, 0.06);
	}

	.letra:active {
		background-color: rgba(0, 0, 0, 0.12);
	}
</style>